.List-Title-Edit {
  display: flex;
  align-items: center;
}

.List-Title-Edit ion-icon {
  cursor: pointer;
  padding: 0.22vw;
  margin: 0.11vw 0.34vw;
  font-size: 2.58vh;
  border-radius: 0.56vw;
  color: rgb(0 0 0 / 50%);
}

.List-Title-Edit ion-icon:hover {
  background: rgb(211 211 211 / 100%);
}

.List-Title-Textarea {
  margin: 0.67vw 0 0.56vw 0.67vw;
  border-radius: 0.34vw;
  border: none;
  resize: none;
  outline: none;
  font-size: 1.68vh;
  padding: 0.56vw;
}

.List-Title-Textarea:focus {
  box-shadow: inset 0 0 0 0.22vw #0079bf;
}
